:root{
    --color-base: #F2E3BC;
    --color-dark: #414535;
    --color-blue: #96BBBB;
    --color-chan: #38a700;
    --color-su: #ff82a9;
}

html {
    font-size: 62.5%;
}

*,
*::before,
*::after {
    box-sizing: border-box; 
}

body {
    background: #fff4c0;
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-rows: 1fr 25vh;
    word-break: keep-all;
    font-size: 2rem;
}

/* max WIDTH HEIGHT = 100% */
p,
.clicked .clue__text, .clicked .selected__clue {
    max-width: 100%;
    max-height: 100%;
}

/* WIDTH HEIGHT = 100% */
#begin, .box, .bag_container, .text,
.text, .bag1, item {
    width: 100%;
    height: 100%;
}

a:link, a:visited, a:hover, a:active{
    text-decoration: inherit;
    color: inherit;
    -webkit-tap-highlight-color: transparent;
}

p {
    margin-block-start: 1px;
    margin-block-end: 1px;
    padding: 1rem 0;
}

#begin {
    position: fixed;
    background: var(--color-blue);
    z-index: 1;
}


/* top */

.top {
    display: flex;
    overflow: hidden;
    position: relative;
}

.icon {
    position: absolute;
    width: 5rem;
    height: 5rem;
}

.menu {
    display: flex;
    flex-direction: column;
    position: fixed;
    flex-wrap: wrap;
    height: fit-content;
}

.menu__icon {
    position: static;
    margin: 1rem;
    fill: var(--color-dark);
}

.menu__list {
    background: var(--color-blue);
    opacity: .5;
    list-style: none;
    padding-left: 0;
    margin: 0 0 1rem 1rem;
    border-radius: 5px;
    color: white;
}

.menu__item {
    padding: 1rem 3rem;
    font-size: 3rem;
}

.collapsible__content {
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all 0.3s;
}

.collapsible--expanded .collapsible__content {
    max-height: 100vh;
    opacity: 1;
}

.collapsible--expanded .menu__icon {
    fill: var(--color-blue);
}

.to-left {
    rotate: 180deg;
}

.to-right {
    right: 0;
}

.to-right, .to-left {
    top: 50%;
    transition: transform 0.3s;
    opacity: 0.70;
}

.to-left:active, .to-right:active {
    transform: scale(1.5);
}

.full_pic {
    width: 100vw;
}

.ahmola {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    height: 6rem;
    max-width: 90%;
    min-width: 25rem;
}

.ahmola1 {
    top: 42%;
    left: 21%;
}

.ahmola2 {
    top: 32%;
    left: 10%;
}

.mg {
    fill: white;
    filter: drop-shadow(0px 0px 4px grey);
    opacity: 0.85;
}

.mg--clicked {
    display: none;
}

.mg--clicked + .input {
    display: block;
}

.input {
    display: none;
}

.input--correct .clue, .input--correct .btn{
    display: none;
}

.clue {
    border: 1px solid var(--color-dark);
    outline: 0;
    border-radius: 5px;
    padding: 1rem 0;
    max-width: fit-content;
    color: var(--color-blue);
    text-align: center;
}

.btn {
    background: var(--color-blue);
    color: white;
    border: 0;
    border-radius: 5px;
    padding: 1rem;
    height: 100%;
}

picture, .pic {
    align-self: center;
}


.click{
    position: fixed;
    left: 0;
    align-self: center;
    overflow: hidden;
}

.click__detective {
    clip-path: ellipse(24% 20% at 53% 49%);
}

.click__chest {
    clip-path: ellipse(14% 7% at 16% 65%);
}

.click__map {
    /* background: var(--color-su); */
    clip-path: ellipse(16% 11% at 14% 36%);
}

.evidence {
    filter: drop-shadow(0px 0px 2px var(--color-dark));
    transition: transform 0.1s;
}

.evidence:active {
    transform: scale(1.05);
}

.bag__item{
    left: 0;
    position: fixed;
    overflow: hidden;
    align-self: center;
}

.item_desc {
    background: var(--color-dark);
    color: var(--color-base);
    position: absolute;
    bottom: 5%;
    padding: 1rem;
    border-radius: 2rem;
    text-align: center;
    line-height: 1.4;
}



/* bottom */

.bottom {
    display: flex;
    padding: 0 10px 10px 10px;
    position: relative;
}

.frog, .bunny {
    top: -10%;
}

.frog {
    right: 85%;
}

.bunny {
    left: 85%;
}

.box {
    display: flex;
    background: var(--color-base);
    border: 5px dotted #b6a989;
    overflow: scroll;
    border-radius: 3rem;
    justify-content: center;
    align-items: center;
}

.text {
    justify-content: center;
    align-items: center;
    padding: 0.5rem;
    text-align: center;
    color: var(--color-dark);
    line-height: 1.4;    
}

.clue__text, .clue__text * {
    max-width: 0;
    margin: 0;
    overflow: scroll;
}

.bag1 {
    display: grid;
    margin: 0;
    padding: 1rem;
    grid-gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
}

.item {
    display: flex;
    margin: 0;
    padding: 0;
    border-radius: 2rem;
    overflow: hidden;
    justify-content: center;
    align-items: center;
}

.item:nth-child(1),
.item:nth-child(3),
.item:nth-child(6),
.item:nth-child(8) {
    background: rgb(56, 167, 0, 0.5);
}

.item:nth-child(2),
.item:nth-child(4),
.item:nth-child(5),
.item:nth-child(7) {
    background: rgb(255, 130, 169, 0.5);
}

.collected__item, .collected__item * {
    width: 100%;
    opacity: 0.5;
}

.selected__item, .selected__item *{
    opacity: 1;
}